.cards {
  height: 400px;
  padding: 80px;
  width: 100%;
  border: 1px solid white;
  overflow-x: scroll;
  overflow-y: hidden;
  cursor: pointer;
  transition: all 0.5s;
  transform: scale(0.98);
  white-space: nowrap;
  background: rgba(255, 255, 255, 0.1);
  perspective: 500px;

  .card {
    width: 150px;
    height: 350px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 80px;
    color: rgba(0, 0, 0, 0.15);
    box-shadow: inset 0 0 0 10px rgba(0, 0, 0, 0.15);
  }

  &.active {
    background: rgba(255, 255, 255, 0.3);
    cursor: grabbing;
    cursor: -webkit-grabbing;
    transform: scale(1);
  }

  .card1 {
    background-color: #ff6666;
    border: 5px solid darken(#ff6666, 5%);
  }
  .card2 {
    background-color: #ff8c66;
    border: 5px solid darken(#ff8c66, 5%);

  }
  .card3 {
    background-color: #ffb366;
    border: 5px solid darken(#ffb366, 5%);

  }
  .card4 {
