    border: 5px solid darken(#ffd966, 5%);

  }
  .card5 {
    background-color: #ffff66;
    border: 5px solid darken(#ffff66, 5%);

  }
  .card6 {
    background-color: #d9ff66;
    border: 5px solid darken(#d9ff66, 5%);

  }
  .card7 {
    background-color: #b3ff66;
    border: 5px solid darken(#b3ff66, 5%);
  }
  .card8 {
    background-color: #8cff66;
    border: 5px solid darken(#8cff66, 5%);
  }
  .card9 {
    background-color: #66ff66;
    border: 5px solid darken(#66ff66, 5%);
  }
  .card10 {
    background-color: #66ff8c;
    border: 5px solid darken(#66ff8c, 5%);
  }
  .card11 {
    background-color: #66ffb3;
    border: 5px solid darken(#66ffb3, 5%);
  }
  .card12 {
    background-color: #66ffd9;
    border: 5px solid darken(#66ffd9, 5%);
  }
  .card13 {
    background-color: #66ffff;
    border: 5px solid darken(#66ffff, 5%);
  }
  .card14 {
