    border: 5px solid darken(#66d9ff, 5%);
  }
  .card15 {
    background-color: #66ccff;
    border: 5px solid darken(#66ccff, 5%);
  }
  .card16 {
    background-color: #66b3ff;
    border: 5px solid darken(#66b3ff, 5%);
  }
  .card17 {
