    border: 5px solid darken(#d966ff, 5%);
  }
  .card22 {
    background-color: #ff66ff;
    border: 5px solid darken(#ff66ff, 5%);
  }
  .card22 {
    background-color: #ff6666;
    border: 5px solid darken(#ff6666, 5%);
  }
}

.card:nth-child(even) {
  transform: scaleX(1.31) rotateY(40deg);
}

.card:nth-child(odd) {
  transform: scaleX(1.31) rotateY(-40deg);
}